export class PokemonService {
    selecionarPokemonPorNome(nome) {
        const url = `https://pokeapi.co/api/v2/pokemon/${nome}`;
        return fetch(url)
            .then((res) => this.processarResposta(res))
            .then((obj) => this.mapearPokemon(obj));
    }
    selecionarPokemons() {
        const url = `https://pokeapi.co/api/v2/pokemon/`;
        return fetch(url)
            .then((res) => this.processarResposta(res))
            .then((obj) => this.mapearListaPokemon(obj.results));
    }
    processarResposta(res) {
        if (res.ok)
            return res.json();
        throw new Error('Pokemon nao encontrado');
    }
    mapearPokemon(obj) {
        return {
            id: obj.id,
            nome: obj.name,
            spriteUrl: obj.sprites.front_default,
            spriteUrlShiny: obj.sprites.front_shiny,
            moves: obj.moves.name
        };
    }
    mapearListaPokemon(objs) {
        const listaDePokemon = objs.map(obj => {
            return this.selecionarPokemonPorNome(obj.name);
        });
        return Promise.all(listaDePokemon);
    }
}
